import React from 'react';
import 'antd/dist/antd.css';
import { connect } from 'react-redux';
import { Layout, Menu, Icon, Avatar, Row, Col, Button } from 'antd';
import AvatarSrc from '../../resources/avatar.png';
import Credentials from '../Credentials';
import Experience from '../Experience';
import Projects from '../Projects';
import Home from '../Home';

const { Content, Sider, } = Layout;

class App extends React.Component{

  onCollapse = () => {
    this.props.dispatch({type: "TOGGLE_SIDER", siderToggleState: !this.props.contentState.siderToggleState, });
  };

  onMenuItemClick = (item) => {
    if(this.props.windowState.siderExpandState !== "fixed"){
      this.props.dispatch({type: "TOGGLE_SIDER", siderToggleState: true, });
    }
    this.props.dispatch({type: "MENU_CLICK", menuState:item.key})
  }

  render(){
    const ContentComponent = (state) => {
      switch(state){
        case "credentials":
          return(<Credentials/>);
        case "experience":
            return(<Experience/>);
        case "projects":
            return(<Projects/>);
        default:
            return(<Home/>);
      }
    }

    return(
      <Layout style={{ minHeight: '100vh', backgroundImage: 'url("'+require('../../resources/background.png')+'")', userSelect:'none',}}>
        <Sider style={{overflow: 'auto', minHeight: '100vh',}} width={this.props.windowState.siderExpandState === 'fixed' ? 326 : window.innerWidth} align='center' collapsible collapsed={this.props.contentState.siderToggleState} onCollapse={this.onCollapse}>
        { this.props.contentState.siderToggleState ? <div/> :
          <div>
            <div style={{width:'100%', paddingTop:'16px', display:'flex', justifyContent:'center', alignItems:'center'}}>
                <Avatar size={256} src={AvatarSrc} alt="avatar.png">
                </Avatar>
            </div>
            <div style={{width:'100%', paddingTop:'16px',color:'white',textAlign:'center',fontFamily: "Times New Roman", fontSize:'16px',}}>
              Christopher Posca
            </div>
            <div style={{width:'100%', paddingTop:'4px',color:'white',textAlign:'center',fontFamily: "Times New Roman", fontSize:'12px',fontStyle:'italic',}}>
              Software Developer
            </div>
            <Row type='flex' justify='space-around' align='middle' style={{padding:'20px'}}>
              <Col span={3}/>
              <Col span={3}>
                <Button type="link" shape="circle" size='large' href='https://www.facebook.com/chris.posca' style={{backgroundColor:'#3b5998', color:"#FFF"}}>
                  <Icon type='facebook' style={{fontSize:'24px',}}/>
                </Button>
              </Col>
              <Col span={3}>
                <Button type="link" shape="circle" size='large' href='https://github.com/cposca' style={{backgroundColor:'#211F1F', color:"#FFF"}}>
                  <Icon type='github' style={{fontSize:'24px',}}/>
                </Button>
              </Col>
              <Col span={3}>
                <Button type="link" shape="circle" size='large' href='https://www.linkedin.com/in/christopher-posca-19676a58' style={{backgroundColor:'#0e76a8', color:"#FFF"}}>
                  <Icon type='linkedin' style={{fontSize:'24px',}}/>
                </Button>
              </Col>
              <Col span={3}/>
            </Row>
          </div>
        };
          <Menu theme="dark" mode="inline" onClick={this.onMenuItemClick}>
              <Menu.Item key="credentials">
                <Icon type="user" />
                <span>Credentials</span>
              </Menu.Item>
              <Menu.Item key="experience">
                <Icon type="tool" />
                <span>Experience</span>
              </Menu.Item>
              <Menu.Item key="projects">
                <Icon type="code" />
                <span>Projects</span>
              </Menu.Item>
          </Menu>
        </Sider>
        <Content style={{margin: '16px', background:"#ffffff88",} }>
          {ContentComponent(this.props.contentState.contentState)}
        </Content>
      </Layout>
    );
  }
}

const mapStateToProps = state => ({
  ...state
});

export default connect(mapStateToProps)(App);
