const setUIState = (width) => {
  if(width < 800){
    return("xs");
  }else if(width<1200){
    return("s");
  }else if(width<1600){
    return("m");
  }
  else if(width<200){
    return("l");
  }
  else{
    return('xl');
  }
}

const setSiderExpandState = (width) => {
  if(width < 800){
    return("full");
  }else {
    return("fixed");
  }
}

const initialState = {
    uiState: setUIState((typeof window === 'object' ? window.innerHeight : null)),
    siderExpandState: setSiderExpandState((typeof window === 'object' ? window.innerWidth : null)),
};

function windowReducer(state = initialState, action) {
    switch (action.type) {
        case "RESIZE_SCREEN":
            return Object.assign({}, state, {
                uiState: setUIState(action.screenWidth),
                siderExpandState: setSiderExpandState(action.screenWidth),
            });
        default:
            return state;
    }
}

export default windowReducer;
