const initialState = {
    siderToggleState: false,
    menuState: "",
    contentState: "home",
};

const setContentState = (menuState) => {
  return menuState;
}

function contentReducer(state = initialState, action) {
    switch (action.type) {
        case "TOGGLE_SIDER":
            return Object.assign({}, state, {
                siderToggleState: action.siderToggleState,
            });
        case "MENU_CLICK":
            return Object.assign({}, state, {
                menuState: action.menuState,
                contentState: setContentState(action.menuState),
            });
        default:
            return state;
    }
}

export default contentReducer;
